
*{
  margin: 0;
  padding: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin: 50px auto 0;
  width: 500px;
}
input{
  width: 50%;
  height: 22px;
  margin-top: 10px;
  border-radius: 3px;
  border: 1px solid #D691F3;
  text-align: center;
  font-size: 14px;
}



